:root {
  --md-sys-color-primary: #2196f3;
  --md-sys-color-primary-muted: #91c6f0;
  --md-sys-color-secondary: #ff4081;
  --background: #f0f0f0;
  --font: #606060;
}

body {
  background: var(--background);
  color: var(--font);
  margin: 0;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 14px;
  display: flex;
}

header {
  background-color: #fff;
  border-radius: 32px;
  width: calc(100% - 48px);
  max-width: 1140px;
  height: 64px;
  margin: 0 auto;
  position: fixed;
  top: 24px;
  left: 0;
  right: 0;
  overflow: hidden;
}

progress-bar div {
  background-color: #ff0;
}

main {
  background-color: green;
  width: calc(100% - 48px);
  max-width: 1140px;
  margin: 112px auto 0;
}

#compute {
  all: unset;
  background-color: var(--md-sys-color-primary);
  z-index: 1;
  border-radius: 12px;
  height: 24px;
  padding: 0 8px;
  position: absolute;
  top: 24px;
  right: 24px;
}
